body {
  margin: 0;
  font-family: "Cormorant SC", serif;
  color: #000;
  background: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
}

header.hero {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
  padding: 3rem 1rem 1rem;
  height: 60vh;
  text-align: center;

  .text {
    text-align: center;
    font-size: 16px;
    font-weight: 600;
    letter-spacing: .7px;

    &.bless {
      margin-bottom: 0.25rem;
    }

    &.song {
      margin-bottom: 0.75rem;
    }
  }

  .middle {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;

    .parents-row {
      display: flex;
      justify-content: space-around;
      align-items: center;
      width: 100%;

      .parent-group {
        display: flex;
        flex-direction: column;
        margin: .5rem 0;

        .parent {
          display: flex;
          justify-content: center;
          align-items: center;
          margin: .5rem 0;

          svg {
            margin-left: .5rem;
            ;
          }
        }
      }
    }
  }

  .patinha-group {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;

    .logo {
      position: relative;
      z-index: 1;
      width: auto;
      height: 60px;
    }

    .patinha {
      position: absolute;
      width: auto;
      height: 90px;
      opacity: 0.5;
      transform: rotate(15deg);
    }
  }

  .image-parent {
    margin: 1rem 0;

    .image {
      width: 100%;
    }
  }

  h2 {
    margin: 0;
    font-size: 32px;
    font-weight: 500;
    margin-top: 0.5rem;
  }
}

section {
  padding: 2rem 1rem;
  max-width: 800px;
  margin: 0 auto;
  width: 100%;

  h2 {
    border-bottom: 2px solid #000;
    padding-bottom: .5rem;
    margin-bottom: 1rem;
    font-style: italic;
    font-family: "Edu NSW ACT Cursive", cursive;
    font-weight: 500;
  }

  p {
    margin: .5rem 0;
  }
}

.mapa iframe {
  width: 100%;
  height: 300px;
  border: none;
}

.btn-registry {
  display: inline-block;
  padding: .75rem 1.5rem;
  border-radius: 2rem;
  text-decoration: none;
  background: #000;
  color: #fff;
  font-weight: bold;
}

.lembrete {
  margin-top: .5rem;
  font-style: italic;
}

.carousel {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 1rem;
}

#contato a {
  color: #000;
  text-decoration: none;
}